; }

footer {
  flex: 0 var(--grid-gutter); }

:root {
  --text-scale: 1.2;
  --text-base-size: 1rem;
  --text-xs: calc(var(--text-base-size) / var(--text-scale) / var(--text-scale));
  --text-sm: calc(var(--text-scale) * var(--text-xs));
  --text-md: calc(var(--text-sm) * var(--text-scale) * var(--text-scale));
  --text-lg: calc(var(--text-scale) * var(--text-md));
  --text-xl: calc(var(--text-scale) * var(--text-lg));
  --text-xxl: calc(var(--text-scale) * var(--text-xl));
  --text-xxxl: calc(var(--text-scale) * var(--text-xxl));
  --text-xxxxl: calc(var(--text-scale) * var(--text-xxxl)); }

:root {
  --space-unit:  1em;
  --space-xxxxs: calc(0.125 * var(--space-unit));
  --space-xxxs:  calc(0.25 * var(--space-unit));
  --space-xxs:   calc(0.375 * var(--space-unit));
  --space-xs:    calc(0.5 * var(--space-unit));
  --space-sm:    calc(0.75 * var(--space-unit));
  --space-md:    calc(1.25 * var(--space-unit));
  --space-lg:    calc(2 * var(--space-unit));
  --space-xl:    calc(3.25 * var(--space-unit));
  --space-xxl:   calc(5.25 * var(--space-unit));
  --space-xxxl:  calc(8.5 * var(--space-unit));
  --space-xxxxl: calc(13.75 * var(--space-unit));
  --component-padding: var(--space-md); }

