.nav-bar {
  align-items: center;
  align-self: start;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  left: 0;
  padding: var(--space-md);
  top: 0;
  flex: 0 80%;
  max-width: 64em; }
  .nav-bar__nav-items {
    background-color: #E3F509;
    background-image: url("https://res.cloudinary.com/makingthings/image/upload/v1597253093/yoga/vector.png");
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: contain;
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    display: grid;
    place-items: center; }
    .nav-bar__nav-items[hidden="hidden"] {
      display: none; }
  .nav-bar__nav-item {
    font-size: var(--text-lg);
    text-align: center;
    display: block; }

.masthead {
  flex: 0 50vw; }
  @media (min-width: 40em) {
    .masthead {
      flex: 0 200px;
      flex: 0 min(200px, 20vw); } }
  .masthead figure svg {
    color: #1ea3a3; }
  .masthead figure svg:last-child {
    width: 50%;
    margin: -10% 0;
    color: #333333; }

.toggle-nav {
  margin-left: auto;
  position: relative;
  z-index: 2; }

.sr-only {
  clip-path: rect(1px, 1px, 1px, 1px);
  clip-path: rect(1px 1px 1px 1px);
  height: 0;
  overflow: hidden;
  position: absolute !important;
  width: 0; }

[x-cloak=""] {
  display: none; }

.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff; }
  @media (min-width: 40em) {
    .fixed {
      transform: translateY(-100%);
      transition: all 0.4s ease; }
      .fixed.visible {
        transform: translateY(0%); } }

@media screen and (max-width: 768px) {
  [x-cloak="mobile"] {
    display: block; } }

.icon {
  width: 1em;
  background-color: transparent;
  color: #066d8e;
  padding: 2px;
  border-radius: 4px; }
  .icon:active {
    color: #E3F509;
    background-color: transparent;
    transform: scale(0.99);
    box-shadow: transparent; }
  .icon:focus {
    outline: none;
    box-shadow: 0 0 0 2px #1ea3a3; }

.toggle-nav {
  font-size: var(--text-xxl); }

@media (hover: hover) {
  .icon:hover {
    color: #b4e9e9;
    background-color: transparent; } }

button {
  font-family: inherit;
  /* For all browsers */
  font-size: 100%;
  /* For all browsers */
  line-height: 1.15;
  /* For all browsers */
  margin: 0;
  /* Firefox and Safari have margin */
  overflow: visible;
  /* Edge hides overflow */
  text-transform: none;
  /* Firefox inherits text-transform */
  -webkit-appearance: button;
  /* Safari otherwise prevents some styles */
  bottom: 0;
  background-color: #066d8e;
  border: none;
  padding: var(--size-sm) var(--size-md); }
  button:active {
    transform: scale(0.9); }
  button:focus {
    outline: none;
    box-shadow: 0 0 0 3px #1ea3a3; }

button::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring {
  outline: 1px dotted ButtonText; }

@media (hover: hover) {
  button:hover {
    color: #b4e9e9;
    background-color: transparent; } }

a:focus {
  outline: none; }

* {
  margin: 0;
  padding: 0;
  list-style-type: none; }

*, *:before, *:after {
  box-sizing: border-box; }

html {
  box-sizing: inherit; }

html, body {
  height: 200%; }

body {
  font-kerning: auto;
  background-color: #ebedec;
  font-family: Poppins, sans-serif;
  font-size: var(--text-base-size); }

svg {
  display: block;
  width: 100%; }

.wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.main {
  flex: 0 var(--grid-gutter);
  height: 50%;
  border: 2px solid pink; }

footer {
  grid-row: 2 / 3 border; }

a, .link {
  color: #066d8e;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  position: relative;
  width: 100%; }

.link:before {
  position: absolute;
  display: block;
  content: "";
  background-color: #066d8e;
  width: 100%;
  height: 20%;
  top: 105%;
  left: 50%;
  transform: translateX(-52%) scaleX(0);
  transform-origin: center;
  transition: all 0.2s ease; }

@media (hover: hover) {
  .link:hover:before {
    transform: translateX(-52%) scaleX(1); } }
